.footer {
    position: relative;
    min-height: 50px;
    display: flex;
    align-items: center;
    padding: 0 6px 0 21px;
    justify-content: space-between;
}

.amount {
    font-size: 13.3333px;
}

.btn-group {
    position: relative;
    z-index: 1;
}

.filter-btn {
    margin: 3px;
    padding: 3px 7px;
    border: 1px solid transparent;
    border-radius: 3px;
    background-color: transparent;
    cursor: pointer;
}

.filter-btn:hover,
.filter-btn.active {
    border-color: pink;
}
